import React from "react"
import '../../assets/css/Test.css'

import {Container, Card, Form, Button, Row, Col, ButtonGroup} from 'react-bootstrap'
import ReactPlaceholder from 'react-placeholder';
import LazyLoad from 'react-lazyload';

import * as JSONRetriever from '../Logic/JSONRetriever'

class PanelPregunta extends React.Component{
  constructor(props){
    super(props)
    this.state = {
      marcada: '',
      correcta: this.props.correcta
    }
    this.onChangeRadio = this.onChangeRadio.bind(this)
  }

  isCorrecta(){

  }

  onChangeRadio(e){
    this.setState({marcada: e.target.value})
  }

  render(){
      const options = this.props.respuestas.map(function(e){
        return(
          <label>
            <input
              type="radio"
              value={e}
              checked={this.state.marcada === e}
              onChange={this.onChangeRadio}
            />
            <span>{e}</span>
          </label>
      )}, this)
      return(
      <Card id='card_pregunta_encontrada'>
        <Card.Header id='card_pregunta_encontrada_header'>Pregunta {this.props.numero}</Card.Header>
        <Card.Body>
            <p>{this.props.enunciado}</p>
            <Container id='container_respuestas_test'> {options} </Container>
        </Card.Body>
        <Card.Footer>
        </Card.Footer>

      </Card>
      )
  }
}



class ContainerPreguntas extends React.Component{
  constructor(props){
    super(props)
    this.state={
      array_card_preguntas: [],
      ready: false
    }
  }

  refreshContainer(){
    var array_resultados = this.props.estado_padre.preguntas_test
    var array_preguntas_aux = []

    for (var i = 0; i < array_resultados.length; i++) {
    	array_preguntas_aux.push(<ContainerPreguntas numero={i} enunciado={array_resultados[i][0].enunciado} correcta={array_resultados[i][0].verdadera} respuestas={array_resultados[i][0].respuestas} key={"pregunta-" + i} />);
    }
    this.setState({array_card_preguntas: array_preguntas_aux, ready: true})

  }

  render(){
    return(
      <ReactPlaceholder type='text' rows={7} ready={this.state.ready} >
        <Container id='container_preguntas'>
          <Row top="xs">
            <Col id='search_result_container'>
              <LazyLoad once>
                  <React.Fragment>
                   {this.state.array_card_preguntas.map(pregunta => (
                     <React.Fragment key={pregunta.props.numero}>
                       <PanelPregunta numero={pregunta.props.numero} enunciado={pregunta.props.enunciado} respuestas={pregunta.props.respuestas} correcta={pregunta.props.correcta}/>
                     </React.Fragment>
                   ))}
                 </React.Fragment>
             </LazyLoad>
            </Col>
          </Row>
        </Container>
      </ReactPlaceholder>
    )
  }

}

export default ContainerPreguntas
